/* GENERAL CONTAINER */
.home {
  width: 100%;
  font-family: Arial, sans-serif;
  background-color: #0f172a; /* dark blue-gray background */
  color: #e2e8f0; /* light text */
  padding: 40px 60px;
  box-sizing: border-box;
}

/* HERO SECTION */
.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 60px;
}

.hero-text {
  width: 55%;
}

.hero-text h1 {
  font-size: 42px;
  margin-bottom: 10px;
  color: #38bdf8; /* bright blue for name */
}

.hero-text h2 {
  font-size: 22px;
  color: #60a5fa; /* lighter blue for role */
  margin-bottom: 20px;
}

.hero-text p {
  font-size: 16px;
  line-height: 1.7;
  margin-bottom: 25px;
  color: #cbd5e1; /* soft text */
}

.hero-buttons {
  display: flex;
  gap: 15px;
}

.hero-image {
  width: 35%;
  text-align: center;
}

.hero-image img {
  width: 250px;
  border-radius: 15px;
  border: 3px solid #38bdf8;
}

/* SECTION STYLE */
.section {
  text-align: center;
  margin-bottom: 50px;
  padding: 30px 0;
}

.section h3 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #38bdf8;
}

.section p {
  font-size: 16px;
  line-height: 1.6;
  max-width: 800px;
  margin: auto;
  color: #cbd5e1;
}

/* LIGHT SECTION (slightly different shade) */
.section.light {
  background-color: #1e293b; /* darker gray-blue */
  padding: 40px 20px;
  border-radius: 10px;
}

/* CARDS (WHAT I DO) */
.cards {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 25px;
}

.card {
  width: 250px;
  background-color: #1e293b;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #334155;
}

.card h4 {
  margin-bottom: 10px;
  color: #38bdf8;
}

.card p {
  color: #cbd5e1;
  font-size: 15px;
}

/* HIGHLIGHTS LIST */
.highlights {
  list-style: none;
  padding-left: 0;
}

.highlights li {
  font-size: 16px;
  margin-bottom: 8px;
  color: #e2e8f0;
}

/* CTA SECTION */
.cta {
  text-align: center;
  padding: 50px 30px;
  background-color: #2563eb; /* brighter blue for CTA */
  color: white;
  border-radius: 12px;
}

.cta h3 {
  font-size: 28px;
  margin-bottom: 15px;
}

.cta p {
  font-size: 16px;
  margin-bottom: 20px;
}

/* BUTTONS */
.btn {
  padding: 10px 24px;
  border-radius: 25px;
  font-size: 14px;
  text-decoration: none;
}

.btn.primary {
  background-color: #38bdf8;
  color: #0f172a;
}

.btn.outline {
  border: 1px solid #38bdf8;
  background-color: transparent;
  color: #38bdf8;
}

/* RESPONSIVE */
@media (max-width: 768px) {
  .hero {
    flex-direction: column;
    text-align: center;
  }

  .hero-text,
  .hero-image {
    width: 100%;
  }

  .hero-image img {
    margin-top: 20px;
  }

  .cards {
    flex-direction: column;
    align-items: center;
  }
}
